body {
  margin: 0;
  overflow: hidden; /* Prevent page scrolling */
  font-family: Arial, sans-serif;
}

.app-container {
  position: relative;
  height: 100vh;
  width: 100vw;
}

header {
  background-color: #282c34;
  color: white;
  padding: 20px;
  text-align: center;
}

header h1 {
  font-size: 2em;
  margin: 0;
}

header p {
  font-size: 1em;
  margin: 5px 0 0;
}

.toolbar {
  position: absolute;
  top: 70px; /* Adjusted to position below the header */
  left: 50%;
  transform: translateX(-50%);
  background: rgba(255, 255, 255, 0.9);
  padding: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  display: flex;
  gap: 5px;
  z-index: 10;
  border-radius: 5px;
}

.toolbar-button {
  background: #007bff;
  color: white;
  border: none;
  padding: 8px 12px; /* Adjusted padding for smaller buttons */
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s ease;
  font-size: 12px; /* Smaller font size */
}

.toolbar-button:hover {
  background: #0056b3;
}

.dropdown {
  position: absolute;
  top: 120px; /* Adjust as necessary to position below the toolbar */
  left: 50%;
  transform: translateX(-50%);
  background: rgba(255, 255, 255, 0.9);
  padding: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  z-index: 10;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.image-selector {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.image-thumbnail {
  width: 50px;
  height: 50px;
  cursor: pointer;
  border: 2px solid transparent;
  border-radius: 5px;
  transition: border 0.3s ease, transform 0.3s ease;
}

.image-thumbnail.selected {
  border: 2px solid #007bff;
  transform: scale(1.1);
}

canvas {
  display: block;
  width: 100%;
  height: calc(100% - 160px); /* Adjust height based on the header and toolbar heights */
}

@media (max-width: 768px) {
  header {
    padding: 15px;
  }

  header h1 {
    font-size: 2.5em;
  }

  header p {
    font-size: 1.2em;
  }

  .toolbar {
    top: 70px; /* Adjust as necessary to position below the header */
    padding: 10px;
    flex-wrap: wrap; /* Allow toolbar items to wrap on smaller screens */
  }

  .toolbar-button {
    padding: 10px 15px; /* Adjusted padding for larger buttons */
    font-size: 14px;
  }

  .dropdown {
    top: 150px; /* Adjust as necessary to position below the toolbar */
  }

  .image-thumbnail {
    width: 60px;
    height: 60px;
  }

  .image-selector {
    flex-direction: row; /* Display images in a row on mobile */
    flex-wrap: wrap; /* Allow images to wrap to the next line */
  }
}
